import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import { withStyles } from '@material-ui/core/styles';
import {
  Typography,
  TextField,
  Button
} from '@material-ui/core';
import { withNamespaces } from 'react-i18next';

import {
  ERROR,
  DEPOSIT_POOL,
  DEPOSIT_POOL_RETURNED,
  WITHDRAW_POOL,
  WITHDRAW_POOL_RETURNED,
  DEPOSIT_ALL_POOL,
  DEPOSIT_ALL_POOL_RETURNED,
  WITHDRAW_ALL_POOL,
  WITHDRAW_ALL_POOL_RETURNED,
  FARM_STAKE,
  FARM_STAKE_RETURNED,
  UNFARM_STAKE,
  UNFARM_STAKE_RETURNED,
  CLAIM_REWARD_STAKE,
  CLAIM_REWARD_STAKE_RETURNED,
  EXIT_AND_CLAIM_REWARD_STAKE,
  EXIT_AND_CLAIM_REWARD_STAKE_RETURNED
} from '../../constants'

import Store from "../../stores";
const emitter = Store.emitter
const dispatcher = Store.dispatcher
const store = Store.store


const styles = theme => ({
  value: {
    cursor: 'pointer'
  },
  value1: {
    fontSize: '2.0rem',
    fontWeight: 'blod'
  },
  actionInput: {
    padding: '0px 0px 12px 0px',
    fontSize: '0.5rem'
  },
  balances: {
    width: '100%',
    textAlign: 'right',
    paddingRight: '20px',
    cursor: 'pointer'
  },
  rewards: {
    width: '100%',
    textAlign: 'right',
    paddingRight: '20px',
    cursor: 'pointer'
  },
  rootContainer: {
    paddingBottom: '12px',
    //display: 'flex',
    flex: '1',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column'
    }
  },
  actionsContainer: {
    paddingBottom: '12px',
    display: 'flex',
    flex: '1',
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'column'
    }
  },
  title: {
    paddingRight: '24px'
  },
  actionButton: {
    height: '47px'
  },
  tradeContainer: {
    flex: 1,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center'
  },
  sepperator: {
    borderBottom: '1px solid #E1E1E1',
    margin: '24px',
    [theme.breakpoints.up('sm')]: {
      width: '40px',
      borderBottom: 'none',
      margin: '0px'
    }
  },
  sepperator1: {
    borderBottom: '1px solid #E1E1E1',
    margin: '24px',
    [theme.breakpoints.up('sm')]: {
      height: '40px',
      borderBottom: 'none',
      margin: '0px'
    }
  },
  sepperator2: {
    borderBottom: '1px solid #E1E1E1',
    margin: '24px',
    [theme.breakpoints.up('sm')]: {
      height: '77px',
      borderBottom: 'none',
      margin: '0px'
    }
  },
  scaleContainer: {
    display: 'flex',
    justifyContent: 'space-between',
    padding: '0px 0px 12px 0px',
    alignItems: 'center',
    flexWrap: 'wrap',
  },
  scale: {
    minWidth: '10px'
  },
  buttonText: {
    fontWeight: '700',
  },
  headingContainer: {
    width: '100%',
    display: 'flex',
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    }
  },
  heading: {
    paddingBottom: '12px',
    flex: 1,
    flexShrink: 0,
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    }
  },
  right: {
    textAlign: 'right'
  },
  buttons: {
    display: 'flex',
    width: '100%'
  },
  disabledContainer: {
    width: '100%',
    paddingTop: '12px',
    textAlign: 'center'
  }
});


class Asset extends Component {

  constructor() {
    super()

    this.state = {
      amount: '',
      amountError: false,
      redeemAmount: '',
      redeemAmountError: false,
      account: store.getStore('account'),

      formType: 'stake',
    }
  }

  componentWillMount() {
    emitter.on(FARM_STAKE_RETURNED, this.depositReturned);
    emitter.on(UNFARM_STAKE_RETURNED, this.withdrawReturned);
    emitter.on(CLAIM_REWARD_STAKE_RETURNED, this.claimReturned);
    emitter.on(EXIT_AND_CLAIM_REWARD_STAKE_RETURNED, this.claimReturned);
    emitter.on(ERROR, this.errorReturned);
  }

  componentWillUnmount() {
    emitter.removeListener(FARM_STAKE_RETURNED, this.depositReturned);
    emitter.removeListener(UNFARM_STAKE_RETURNED, this.withdrawReturned);
    emitter.removeListener(CLAIM_REWARD_STAKE_RETURNED, this.claimReturned);
    emitter.removeListener(EXIT_AND_CLAIM_REWARD_STAKE_RETURNED, this.claimReturned);
    emitter.removeListener(ERROR, this.errorReturned);
  };

  depositReturned = () => {
    this.setState({ loading: false, amount: '' })
  };

  withdrawReturned = (txHash) => {
    this.setState({ loading: false, redeemAmount: '' })
  };

  claimReturned = (txHash) => {
    this.setState({ loading: false });
  };

  errorReturned = (error) => {
    this.setState({ loading: false })
  };

  onTabChange = (type) => {
    this.setState({
      formType: type,
    })
  }

  onStakeMax = (asset) => {
    const max = asset.yourBptBalance ? (Math.floor(asset.yourBptBalance*100000000)/100000000).toFixed(8) : '0.0000'
    this.setState({
      amount: max,
    })
  }

  onUnstakeMax = (asset) => {
    const max = (asset.yourBptFarmed).toFixed(4)
    this.setState({
      redeemAmount: max,
    })
  }

  renderForm() {

    const { classes, asset } = this.props;
    const {
      account,
      amount,
      amountError,
      redeemAmount,
      redeemAmountError,
      loading,
      formType
    } = this.state

    return (
      <div className="window">
        <div className="back-bar">
          <span className="back-btn" onClick={this.props.history.goBack}>
            <img src={ require('../../assets/back.png') }/>
            Back</span>
        </div>
        <div className="reward-area">
          <div className="reward-area-left">
            <div className="stake">
              <label>Your Staked </label>
              <p>
                { (asset.yourBptFarmed).toFixed(4) }  { asset.bptSymbol }
              </p>
            </div>
            <div className="balance">
              <label>Your Balance</label>
              <p>{ (asset.yieldTokenBalance ? (Math.floor(asset.yieldTokenBalance*100000000)/100000000).toFixed(4) : '0.0000') } { asset.yieldTokenSymbol }</p>
            </div>
          </div>
          {/* <div className="reward-area-right">
            <button className="claim-btn" onClick={this.onClaim}>Claim</button>
            <div className="rewords">
              <label>Your Rewards</label>
              <p>{ (asset.yieldTokenClaimable).toFixed(4) } { asset.yieldTokenSymbol }</p>
            </div>
          </div> */}
        </div>

        <div className="stake-area">
          <div className="stake-form">
            <div className="tabs">
              <div className={`tab-btn ${formType === 'stake' && 'active'}`}
                onClick={ () => this.onTabChange('stake') }
                >STAKE</div>
              <div className={`tab-btn ${formType === 'unstake' && 'active'}`}
                onClick={ () => this.onTabChange('unstake') }
                >UNSTAKE</div>
            </div>

            {formType === 'stake' ? (
              <div>
                <div className="form-inner">
                  <div className="tip">
                    <span>Input</span> 
                    <span>Balance: 
                      {(asset.yourBptBalance ? (Math.floor(asset.yourBptBalance*100000000)/100000000).toFixed(4) : '0.0000')} 
                      {/* { asset.bptSymbol } */}
                    </span>
                  </div>
                  {amountError}
                  <div className="input-wrapper">
                    <input type="text" 
                      id='amount'
                      value={ amount }
                      onChange={ this.onChange }
                      disabled={ loading || asset.disabled }
                      placeholder="0.00"
                      variant="outlined"
                      onKeyDown={ this.inputKeyDown }
                    />
                      <button className="btn" onClick={() => this.onStakeMax(asset)}>MAX</button>
                      {/* <span className="unit"> { asset.bptSymbol }</span>   */}
                  </div>
                </div>
                {account.address ? 
                  <button className="submit-btn" onClick={this.onDeposit}>Stake</button> :
                  <button className="submit-btn">Connect wallet</button>
                }
              </div>
            ) : (
              <div>
                <div className="form-inner">
                  <div className="tip">
                    <span>Input</span> 
                    <span>Balance: 
                      { (asset.yourBptFarmed).toFixed(4) } 
                      {/* { asset.bptSymbol } */}
                    </span>
                  </div>
                  {redeemAmountError}
                  <div className="input-wrapper">
                    <input 
                      type="text" 
                      id='redeemAmount'
                      value={ redeemAmount }
                      onChange={ this.onChange }
                      disabled={ loading }
                      placeholder="0.00"
                      variant="outlined"
                      onKeyDown={ this.inputRedeemKeyDown }
                    />
                      <button className="btn" onClick={() => this.onUnstakeMax(asset)}>MAX</button>
                      {/* <span className="unit"> { asset.bptSymbol }</span>   */}
                  </div>
                </div>
                {account.address ? 
                  <button className="submit-btn" onClick={this.onWithdraw}>Unstake</button> :
                  <button className="submit-btn">Connect wallet</button>
                }
              </div>
            )}
            

          </div>
          <div className="stake-pool">
            <h2 className="section-title">LP Token Seed Pool</h2>
            <ul className="pool-list">
              <li class="claim-row">
                <div className="rewords">
                  <label>Your Rewards</label>&nbsp;
                  <p> <em>{ (asset.yieldTokenClaimable).toFixed(4) }</em> { asset.yieldTokenSymbol }</p>
                </div>
                <button className="claim-btn" onClick={this.onClaim}>Claim</button>
              </li>
              <li>
                Your Estimated 24h Rewards <span>0.000000 SDW</span>
              </li>
              <li>
                Current Epoch Rewards <span>{asset.epochRewards} SDW</span>
              </li>
              <li>
                {asset.description}
                {/* Description <span>WETH Seed Pool</span> */}
              </li>
              <li>
                Pool State <span>{asset.poolState}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    )
  }

  render() {

    const { classes, asset } = this.props;
    const {
      account,
      amount,
      amountError,
      redeemAmount,
      redeemAmountError,
      loading
    } = this.state

    return this.renderForm();
    return (
      <div className={ classes.rootContainer }>
        <div className={ classes.actionsContainer }>
          <div className={ classes.tradeContainer }>
            {!asset.disabled && 
            <div className={ classes.balances }>
                <Typography variant='h4' 
                  onClick={ () => { this.setAmount(100) } } 
                  className={ classes.value } noWrap>{ 'Balance0: '+ (asset.yourBptBalance ? (Math.floor(asset.yourBptBalance*100000000)/100000000).toFixed(8) : '0.0000') } { asset.bptSymbol }
                </Typography>
            </div>}
            <TextField
              fullWidth
              className={ classes.actionInput }
              id='amount'
              value={ amount }
              error={ amountError }
              onChange={ this.onChange }
              disabled={ loading || asset.disabled }
              placeholder="0.00"
              variant="outlined"
              onKeyDown={ this.inputKeyDown }
            />
            <div className={ classes.scaleContainer }>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading || asset.disabled }
                color="primary"
                onClick={ () => { this.setAmount(25) } }>
                <Typography variant={'h5'}>25%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading || asset.disabled }
                color="primary"
                onClick={ () => { this.setAmount(50) } }>
                <Typography variant={'h5'}>50%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading || asset.disabled }
                color="primary"
                onClick={ () => { this.setAmount(75) } }>
                <Typography variant={'h5'}>75%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading || asset.disabled }
                color="primary"
                onClick={ () => { this.setAmount(100) } }>
                <Typography variant={'h5'}>100%</Typography>
              </Button>
            </div>
            <div className={ classes.buttons }>
              <Button
                className={ classes.actionButton }
                variant="outlined"
                color="primary"
                disabled={ loading || !account.address || asset.balance <= 0 || asset.depositDisabled === true }
                onClick={ this.onDeposit }
                fullWidth
                >
                <Typography className={ classes.buttonText } variant={ 'h5'} color={asset.disabled?'':'secondary'}>Farm</Typography>
              </Button>
              { asset.version === 2 &&
                <Button
                  className={ classes.actionButton }
                  variant="outlined"
                  color="primary"
                  disabled={ loading || !account.address || asset.balance <= 0 || asset.depositDisabled === true }
                  onClick={ this.onDepositAll }
                  fullWidth
                  >
                  <Typography className={ classes.buttonText } variant={ 'h5'} color={asset.disabled?'':'secondary'}>Farm All</Typography>
                </Button>
              }
            </div>
            { asset.depositDisabled === true &&
              <div className={classes.disabledContainer}>
                <Typography variant='h4'>Deposits are currently disabled for this vault</Typography>
              </div>
            }
          </div>
          <div className={ classes.sepperator }></div>
          <div className={classes.tradeContainer}>
            <div className={ classes.balances }>
              <Typography variant='h4' onClick={ () => { this.setRedeemAmount(100) } }  className={ classes.value } noWrap>Farmed: { (asset.yourBptFarmed).toFixed(8) } { asset.bptSymbol } </Typography>
            </div>
            <TextField
              fullWidth
              className={ classes.actionInput }
              id='redeemAmount'
              value={ redeemAmount }
              error={ redeemAmountError }
              onChange={ this.onChange }
              disabled={ loading }
              placeholder="0.00"
              variant="outlined"
              onKeyDown={ this.inputRedeemKeyDown }
            />
            <div className={ classes.scaleContainer }>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading }
                color="primary"
                onClick={ () => { this.setRedeemAmount(25) } }>
                <Typography variant={'h5'}>25%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading }
                color="primary"
                onClick={ () => { this.setRedeemAmount(50) } }>
                <Typography variant={'h5'}>50%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading }
                color="primary"
                onClick={ () => { this.setRedeemAmount(75) } }>
                <Typography variant={'h5'}>75%</Typography>
              </Button>
              <Button
                className={ classes.scale }
                variant='text'
                disabled={ loading }
                color="primary"
                onClick={ () => { this.setRedeemAmount(100) } }>
                <Typography variant={'h5'}>100%</Typography>
              </Button>
            </div>
            <div className={ classes.buttons }>
              <Button
                className={ classes.actionButton }
                variant="outlined"
                color="primary"
                disabled={ loading || !account.address || asset.yourBptFarmed <= 0 }
                onClick={ this.onWithdraw }
                fullWidth
                >
                <Typography className={ classes.buttonText } variant={ 'h5'} color='secondary'>Unfarm</Typography>
              </Button>
              { asset.version === 2 &&
                <Button
                  className={ classes.actionButton }
                  variant="outlined"
                  color="primary"
                  disabled={ loading || !account.address || asset.pooledBalance <= 0 }
                  onClick={ this.onWithdrawAll }
                  fullWidth
                  >
                  <Typography className={ classes.buttonText } variant={ 'h5'} color='secondary'>Unfarm All</Typography>
                </Button>
              }
            </div>
          </div>
        </div>

        <div className={ classes.sepperator1 }></div>
        
        <div className={ classes.actionsContainer }>
          <div className={ classes.tradeContainer }>
            <div className={ classes.sepperator2 }></div>
            <div className={ classes.buttons }>
              <Button
                className={ classes.actionButton }
                variant="outlined"
                color="primary"
                disabled={ loading || !account.address || asset.balance <= 0 || asset.depositDisabled === true }
                onClick={ this.onClaim }
                fullWidth
                >
                <Typography className={ classes.buttonText } variant={ 'h5'} color={asset.disabled?'':'secondary'}>Claim Reward</Typography>
              </Button>
            </div>
          </div>
          <div className={ classes.sepperator }></div>
          <div className={classes.tradeContainer}>
            <div className={ classes.balances }>
              <Typography variant='h4' className={ classes.value } noWrap>Rewards Available</Typography>
            </div>
            <div className={ classes.balances }>
              <Typography variant='h3' className={ classes.value1 } noWrap>{ (asset.yieldTokenClaimable).toFixed(8) } { asset.yieldTokenSymbol } </Typography>
            </div>
            <div className={ classes.balances }>
              <Typography variant='h4' className={ classes.value } noWrap>+{ (asset.yourBptFarmed).toFixed(8) } { asset.bptSymbol } </Typography>
            </div>
            
            <div className={ classes.buttons }>
              <Button
                className={ classes.actionButton }
                variant="outlined"
                color="primary"
                disabled={ loading || !account.address || asset.pooledBalance <= 0 }
                onClick={ this.onExitAndClaim }
                fullWidth
                >
                <Typography className={ classes.buttonText } variant={ 'h5'} color='secondary'>Claim Reward And Unfarm All</Typography>
              </Button>
            </div>
          </div>
        </div>
      </div>
    
    )
  };

  onChange = (event) => {
    let val = []
    val[event.target.id] = event.target.value
    this.setState(val)
  }

  inputKeyDown = (event) => {
    if (event.which === 13) {
      this.onInvest();
    }
  }

  onDeposit = () => {
    this.setState({ amountError: false })

    const { amount } = this.state
    const { asset, startLoading } = this.props

    if(!amount || isNaN(amount) || amount <= 0 || amount > asset.balance) {
      this.setState({ amountError: true })
      return false
    }

    this.setState({ loading: true })
    startLoading()
    dispatcher.dispatch({ type: FARM_STAKE, content: { amount: amount, asset: asset } })
  }

  onDepositAll = () => {
    const { asset, startLoading } = this.props

    this.setState({ loading: true })
    startLoading()
    dispatcher.dispatch({ type: FARM_STAKE, content: { asset: asset } })
  }

  onWithdraw = () => {
    this.setState({ redeemAmountError: false })

    const { redeemAmount } = this.state
    const { asset, startLoading  } = this.props

    if(!redeemAmount || isNaN(redeemAmount) || redeemAmount <= 0 || redeemAmount > asset.yourBptFarmed) {
      this.setState({ redeemAmountError: true })
      return false
    }

    this.setState({ loading: true })
    startLoading()

    dispatcher.dispatch({ type: UNFARM_STAKE, content: { amount: redeemAmount, asset: asset } })
  }

  onWithdrawAll = () => {
    const { asset, startLoading } = this.props

    this.setState({ loading: true })
    startLoading()
    dispatcher.dispatch({ type: UNFARM_STAKE, content: { asset: asset } })
  }
  
  onClaim = () => {
    const { asset, startLoading  } = this.props

    this.setState({ loading: true })
    startLoading()

    dispatcher.dispatch({ type: CLAIM_REWARD_STAKE, content: { asset: asset } })
  }

  onExitAndClaim = () => {
    const { asset, startLoading  } = this.props

    this.setState({ loading: true })
    startLoading()

    dispatcher.dispatch({ type: EXIT_AND_CLAIM_REWARD_STAKE, content: { asset: asset } })
  }

  setAmount = (percent) => {
    if(this.state.loading) {
      return
    }

    const { asset } = this.props

    const balance = asset.yourBptBalance
    let amount = balance*percent/100
    amount = Math.floor(amount*100000000)/100000000;

    this.setState({ amount: amount.toFixed(8) })
  }

  setRedeemAmount = (percent) => {
    if(this.state.loading) {
      return
    }

    const balance = this.props.asset.yourBptFarmed
    let amount = balance*percent/100
    amount = Math.floor(amount*10000)/10000;

    this.setState({ redeemAmount: amount.toFixed(8) })
  }
}

export default withNamespaces()(withRouter(withStyles(styles, { withTheme: true })(Asset)));
