class Stake extends Component {

  constructor(props) {
    super()

    const account = store.getStore('account')
    this.state = {
      assets: store.getStore('stakeAssets'),
      account: account,
      modalOpen: false,
      snackbarType: null,
      snackbarMessage: null,
      value: 1,
    }
    if(account && account.address) {
      dispatcher.dispatch({ type: GET_STAKE_BALANCES, content: {} })
    }
  }
  componentWillMount() {
    emitter.on(FARM_STAKE_RETURNED, this.showHash);
    emitter.on(UNFARM_STAKE_RETURNED, this.showHash);
    emitter.on(CLAIM_REWARD_STAKE_RETURNED, this.showHash);
    emitter.on(EXIT_AND_CLAIM_REWARD_STAKE_RETURNED, this.showHash);
    emitter.on(ERROR, this.errorReturned);
    emitter.on(STAKE_BALANCES_RETURNED, this.balancesReturned);
    emitter.on(CONNECTION_CONNECTED, this.connectionConnected);
    emitter.on(CONNECTION_DISCONNECTED, this.connectionDisconnected);

  }

  componentWillUnmount() {
    emitter.removeListener(FARM_STAKE_RETURNED, this.showHash);
    emitter.removeListener(CLAIM_REWARD_STAKE_RETURNED, this.showHash);
    emitter.removeListener(EXIT_AND_CLAIM_REWARD_STAKE_RETURNED, this.showHash);
    emitter.removeListener(ERROR, this.errorReturned);
    emitter.removeListener(CONNECTION_CONNECTED, this.connectionConnected);
    emitter.removeListener(CONNECTION_DISCONNECTED, this.connectionDisconnected);
    emitter.removeListener(STAKE_BALANCES_RETURNED, this.balancesReturned);
  };

  refresh() {
    dispatcher.dispatch({ type: GET_STAKE_BALANCES, content: {} })
  }

  balancesReturned = (balances) => {
    this.setState({ assets: store.getStore('stakeAssets') })
    setTimeout(this.refresh, 30000);
  };

  connectionConnected = () => {
    const { t } = this.props
    this.setState({ account: store.getStore('account') })

    dispatcher.dispatch({ type: GET_STAKE_BALANCES, content: {} })

    const that = this
    setTimeout(() => {
      const snackbarObj = { snackbarMessage: t("Unlock.WalletConnected"), snackbarType: 'Info' }
      that.setState(snackbarObj)
    })
  };

  connectionDisconnected = () => {
    this.setState({ account: store.getStore('account') })
  }

  errorReturned = (error) => {
    const snackbarObj = { snackbarMessage: null, snackbarType: null }
    this.setState(snackbarObj)
    this.setState({ loading: false })
    const that = this
    setTimeout(() => {
      const snackbarObj = { snackbarMessage: error.toString(), snackbarType: 'Error' }
      that.setState(snackbarObj)
    })
  };

  showHash = (txHash) => {
    console.log("----------------------farmStake showHash:" + txHash + " -----------------");
    const snackbarObj = { snackbarMessage: null, snackbarType: null }
    this.setState(snackbarObj)
    this.setState({ loading: false })
    const that = this
    setTimeout(() => {
      const snackbarObj = { snackbarMessage: txHash, snackbarType: 'Hash' }
      that.setState(snackbarObj)
    })
    console.log("----------------------farmStake done -----------------");
  };

  render() {
    const { classes, t } = this.props;
    const {
      loading,
      account,
      modalOpen,
      snackbarMessage,
    } = this.state

    var address = null;
    if (account.address) {
      address = account.address.substring(0,6)+'...'+account.address.substring(account.address.length-4,account.address.length)
    }

    return (
      <div className={ classes.root }>
        <div className={ classes.investedContainer }>

          { account.address &&
              <div className={ classes.addressContainer } onClick={this.overlayClicked}>
                {address ? 'Wallet' : <div className={ classes.connectWalletBar }>
                  You haven't connected a wallet. <button className={classes.connectBtn}>Connect wallet</button>
                </div>}
                <Typography variant={ 'h4'} className={ classes.walletAddress } noWrap>{ address }</Typography>
              </div>
          }

          { !account.address &&
            <div className={ classes.introCenter }>
              <Typography variant='h3'>Vaults. Simple.</Typography>
            </div>
          }
          {!account.address &&
            <div className={ classes.connectContainer }>
              <Button
                className={ classes.actionButton }
                variant="outlined"
                color="primary"
                disabled={ loading }
                onClick={ this.overlayClicked }
                >
                <Typography className={ classes.buttonText } variant={ 'h5'}>{ t('InvestSimple.Connect') }</Typography>
              </Button>
            </div>
          }
          {/* { account.address && this.renderAssetBlocks() } */}
          { account.address && this.renderAssetTable() }
        </div>
        { loading && <Loader /> }
        { modalOpen && this.renderModal() }
        { snackbarMessage && this.renderSnackbar() }
      </div>
    )
  };

  onChange = (event) => {
    let val = []
    val[event.target.id] = event.target.checked
    this.setState(val)
  };

  toDetail = () => {
    
  }

  renderAssetTable = () => {

    const { assets, expanded } = this.state
    const { classes } = this.props
    const width = window.innerWidth


    const tableRows = assets.map((asset) => {
      return (
        <tr key={asset.id} onClick={() => this.props.history.push(`/stake/${asset.id}`)}>
          <td className="cell-pool">
            <div className="cell-inner">
              <div className="logo-wrapper">
                <img 
                    src={ require('../../assets/'+asset.yieldTokenSymbol+'-logo.png') }
                     alt=""/>
              </div>
              <div className="wrapper">
                <h3>LP Token Seed Pool</h3>
                <p>LP Token</p>
              </div>
            </div>
          </td>
          <td className="cell-staked">
            <div className="cell-inner">
              <h3>{asset.yourBptFarmed}LP Token</h3>
              <p>0.00% of Total Supply </p>
            </div>
          </td>
          <td className="cell-per">
            <div className="cell-inner">
              <div className="logo-wrapper">
                <img src="/img/sdw/lp_token.png" alt=""/>
              </div>
              <div className="wrapper">
                <h3>{asset.yieldTokenClaimable}</h3>
                <p>$SDW/hour</p>
              </div>
            </div>
          </td>
          <td className="cell-roi">
            <div className="cell-inner">
              <p>Houry: -.-%</p>
              <p>Daily: { (asset.roiInDay).toFixed(4) }%</p>
              <p>Yearly: { (asset.roiInYear).toFixed(4) }%</p>
            </div>
          </td>
        </tr>
      )})

    return (<table className="stake-table">
      <thead>
        <tr>
          <th className="head-pool">Pool</th>
          <th className="head-staked">Staked</th>
          <th className="head-per">Yield per $1000</th>
          <th className="head-roi">ROI</th>
        </tr>
      </thead>
      <tbody>
        {tableRows}
      </tbody>
    </table>)
  }


  handleChange = (id) => {
    this.setState({ expanded: this.state.expanded === id ? null : id })
  }

  startLoading = () => {
    this.setState({ loading: true })
  }

  renderSnackbar = () => {
    var {
      snackbarType,
      snackbarMessage
    } = this.state
    return <Snackbar type={ snackbarType } message={ snackbarMessage } open={true}/>
  };

  renderModal = () => {
    return (
      <UnlockModal closeModal={ this.closeModal } modalOpen={ this.state.modalOpen } />
    )
  }

  overlayClicked = () => {
    this.setState({ modalOpen: true })
  }

  closeModal = () => {
    this.setState({ modalOpen: false })
  }
}

export default withNamespaces()(withRouter(withStyles(styles)(Stake)));
